<template>
  <div class="sliders">
      <div class="slider" v-for="(type, index) in shows" :key="type">
          <h2 class="slider-heading">{{ sectionTitle[index] }}</h2>
          <div class="slider-wrapper">
              <div class="slider-item" v-on:click="showTab()" v-for="show in type.show_content" :key="show">
                  <div class="box-art">
                      <h3 class="box-title">{{ show.title }}</h3>
                      <img :src="require('/public/browse/'+show.image)" alt="">
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            shows: [],
            log: console.log,
        }
    },
    mounted() {
        fetch('https://my-json-server.typicode.com/Raia17/animes/db')
            .then(res => res.json())
            .then(data => this.shows = data.shows)
            .catch(err => console.log(err.message));
    },
    methods: {
    },
    props: {
        sectionTitle: Array,
    }
}
</script>

<style>
.slider {
  margin-block: 4rem;
  padding-left: 5%;
}
.slider-heading {
  color: #fff;
  margin-left: 3rem;
  font-size: 2rem;
  margin-block: 1.2rem;
}
.slider-wrapper {
  display: flex;
  justify-content: flex-start;
  overflow-y: hidden;
  overflow-x: hidden;
  position: relative;
  gap: 0.6rem;
}
.slider-item {
  cursor: pointer;
}
.box-art {
  height: 500px;
  width: 324px;
  position: relative;
}
.box-title {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.85) 80%);
  width: 100%;
  height: 55%;
  padding-inline: 10px;
  color: #fff;
  text-transform: capitalize;
  text-align: center;
  font-size: 1.4rem;
  bottom: 0;
  border-radius: 5px;
}
.box-art > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.slider-handler {
  cursor: pointer;
  position: absolute;
  font-size: 6rem;
  color: #fff;
  height: 100%;
  z-index: 100;
  top: 180px;
  border-radius: 50%;
  text-shadow: 0 0 10px white;
}
.slider-handler.next {
  right: 10px;
}
.slider-handler.previous {
  left: 10px;
}
</style>